

.wrapper{
  max-width: 1000px;
  margin:0 auto;
display: flex;
justify-content: center;
align-items: center;
  height: 100vh;

}

.row{
  display: flex;
  flex-direction: row;
  justify-content: center;

}

.board{
padding:20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display:none;

}

input{
  padding:10px;
  width:300px;
}

h1{
  margin-top: 30px;
}

.startButton{
  width:200px;
  padding:10px;
  font-size: 20px;
  margin-top: 30px;
  border-radius: 3px;
  margin-bottom: 30px;
  background: lightblue;
}

.restart{
  display:none;
  font-size: 18px;
  margin:10px;
  width:200px;
  border-radius: 3px;
  padding:10px 20px;
  background: lightblue;
}
.newGame{
  width:200px;
  border-radius: 3px;
  font-size: 18px;
  padding:10px 20px;
  display:none;
  background: lightblue;
}

.status{
  text-align: center;
  color:blue;
  font-weight: 700;
  font-size: 24px;
  display: none;
}

.error{
  color:red;
  display:none;
}

.helper{
  color:rgba(0, 0, 0, 0.418);
  font-size: 12px;
}

.intro{
 
 

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
 
}

.tile{
  display: flex;
  justify-content: center;
  align-items: center;
  width:30px;
  height:30px;
  background: black;
  text-align: center;
  font-weight: bold;
  border:1px solid white;
}

.tile:hover{
  background: rgba(0, 0, 0, 0.788);
}

h1,h2{
  margin:0px;
  padding:0px;
}